import React, { useState, ChangeEvent, useEffect } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
} from '@mui/material';
import { Cluster } from '../types/types';

interface ClusterFormProps {
  open: boolean;
  cluster?: Cluster;
  onClose: () => void;
  onSubmit: (cluster: Partial<Cluster>) => Promise<void>;
}

const ClusterForm = ({ open, cluster, onClose, onSubmit }: ClusterFormProps) => {
  const [formData, setFormData] = useState<Partial<Cluster>>({
    name: '',
    owner: '',
    apiServer: '',
    businessName: '',
    token: '',
    notes: ''
  });

  //  cluster  open 
  useEffect(() => {
    if (cluster && open) {
      setFormData({
        ...cluster,
        token: '' //  token
      });
    } else if (!open) {
      // 
      setFormData({
        name: '',
        owner: '',
        apiServer: '',
        businessName: '',
        token: '',
        notes: ''
      });
    }
  }, [cluster, open]);

  const [submitting, setSubmitting] = useState(false);

  const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const isFormValid = () => {
    if (cluster) {
      // tokentoken
      return formData.name && 
             formData.owner && 
             formData.apiServer && 
             formData.businessName;
    }
    // 
    return formData.name && 
           formData.owner && 
           formData.apiServer && 
           formData.businessName && 
           formData.token;
  };

  const handleSubmit = async () => {
    try {
      setSubmitting(true);
      // tokentoken
      if (cluster && !formData.token) {
        const { token, ...dataWithoutToken } = formData;
        await onSubmit(dataWithoutToken);
      } else {
        await onSubmit(formData);
      }
    } finally {
      setSubmitting(false);
    }
  };

  return (
    <Dialog open={open} onClose={onClose} maxWidth="sm" fullWidth>
      <DialogTitle>
        {cluster ? '' : ''}
      </DialogTitle>
      <DialogContent>
        <TextField
          fullWidth
          required
          name="name"
          label=""
          value={formData.name || ''}
          onChange={handleChange}
          margin="normal"
          error={!formData.name}
          helperText={!formData.name ? '' : ''}
        />
        <TextField
          fullWidth
          required
          name="owner"
          label=""
          value={formData.owner || ''}
          onChange={handleChange}
          margin="normal"
          error={!formData.owner}
          helperText={!formData.owner ? '' : ''}
        />
        <TextField
          fullWidth
          required
          name="apiServer"
          label="API"
          value={formData.apiServer || ''}
          onChange={handleChange}
          margin="normal"
          error={!formData.apiServer}
          helperText={!formData.apiServer ? 'API' : ''}
          placeholder=": https://kubernetes.default.svc:6443"
        />
        <TextField
          fullWidth
          required
          name="businessName"
          label=""
          value={formData.businessName || ''}
          onChange={handleChange}
          margin="normal"
          error={!formData.businessName}
          helperText={!formData.businessName ? '' : ''}
        />
        <TextField
          fullWidth
          required={!cluster}
          name="token"
          label=""
          value={formData.token || ''}
          onChange={handleChange}
          margin="normal"
          error={!cluster && !formData.token}
          helperText={cluster ? '' : ''}
          type="password"
        />
        <TextField
          fullWidth
          name="notes"
          label=""
          value={formData.notes || ''}
          onChange={handleChange}
          margin="normal"
          multiline
          rows={3}
        />
      </DialogContent>
      <DialogActions>
        <Button 
          onClick={onClose} 
          color="inherit"
          disabled={submitting}
        >
          
        </Button>
        <Button 
          onClick={handleSubmit} 
          color="primary"
          disabled={!isFormValid() || submitting}
        >
          {submitting ? '...' : ''}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default ClusterForm; 